@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.main-banner
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  height: calc(100vh - 80px)
  margin-top: 80px
  position: relative
  @media (max-width: 960px)
    margin-top: 62px
    height: calc(100vh - 62px)
    height: calc((var(--vh, 1vh) * 100) - 62px)
  .container
    padding-top: 5.65%
    position: relative
    z-index: 1
  &__title
    text-align: center
    font-size: 30px
    line-height: 30px
    letter-spacing: 2px
    text-transform: uppercase
    font-weight: 400
    color: $dark-black
    @media (max-width: 768px)
      font-size: 24px
      line-height: 25px
    @media (max-width: 414px)
      font-size: 22px
      line-height: 20px
    @media (max-width: 360px)
      font-size: 18px
      line-height: 20px
    span
      display: block
      font-weight: 300
      font-size: 80px
      line-height: 80px
      margin-top: 10px
      letter-spacing: 0
      @media (max-width: 1400px)
        font-size: 60px
        line-height: 60px
      @media (max-width: 768px)
        font-size: 50px
        line-height: 50px
      @media (max-width: 414px)
        font-size: 35px
        line-height: 35px
      @media (max-width: 360px)
        font-size: 30px
        line-height: 30px
  .scroll-btn
    position: absolute
    bottom: 0
    left: 0
    right: 0
    margin: 0 auto
