/* settings */
$burder-color: #fff

.hamburger
  padding: 0
  display: inline-flex
  align-items: center
  justify-content: center
  cursor: pointer
  transition-property: opacity, filter
  transition-duration: 0.15s
  transition-timing-function: linear
  font: inherit
  color: inherit
  text-transform: none
  background-color: transparent
  border: 0
  overflow: visible
  position: relative
  width: 32px
  height: 32px
  border-radius: 50%
  z-index: 20
  margin: 0 0 0 auto
  @media (max-width: 960px)
    display: inline-flex !important
  &:hover
    opacity: 1
  &.is-active
    &:hover
      opacity: 1
    .hamburger-inner
      background-color: #fff
      &::before, &::after
        background-color: #fff
.hamburger-box
  width: 32px
  height: 45px
  display: inline-block
  position: relative
.hamburger-inner
  display: block
  top: 50%
  width: 32px
  height: 2px
  background-color: $burder-color
  border-radius: 4px
  position: absolute
  transition-timing-function: ease
  transition: transform .15s
  &::before, &::after
    width: 32px
    height: 2px
    background-color: $burder-color
    border-radius: 4px
    position: absolute
    transition-property: transform
    transition-duration: 0.15s
    transition-timing-function: ease
  &::before, &::after
    content: ""
    display: block
  &::before
    top: -10px
/* slide animate */
.hamburger--slider
  .hamburger-inner
    top: 12px
    &::before
      top: 8px
      right: 0
      width: 32px
      transition-property: transform, opacity
      transition-timing-function: ease
      transition-duration: 0.15s
    &::after
      top: 16px
  &.is-active .hamburger-inner
    transform: translate3d(4px,8px,0) rotate(45deg)
    height: 2px
    width: 25px
    &::before
      transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0)
      opacity: 0
    &::after
      transform: translate3d(0, -16px, 0) rotate(-90deg)
      height: 2px
      width: 25px