.header
  position: fixed
  top: 0
  left: 0
  width: 100vw
  background-color: $dark-gray
  padding: 13px 0
  z-index: 10
  transition: padding .3s
  &__content
    display: flex
    align-items: center
    justify-content: flex-start
    flex-wrap: wrap
    max-width: 1840px
    margin: 0 auto
    @media (max-width: 1840px)
      padding: 0 15px
    .logo-link
      display: inline-flex
      align-items: center
      justify-content: flex-start
      position: relative
      &:after
        content: ''
        display: inline-block
        width: 1px
        height: 26px
        background-color: $bronze-gold
        margin: 5px 0 5px 28px
        @media (max-width: 380px)
          margin: 5px 0 5px 15px
      .logo-image
        display: inline-block
        width: auto
        max-width: 100%
        @media (max-width: 360px)
          max-width: 100px
    .add-link
      display: inline-block
      color: #fff
      font-size: 12px
      line-height: 12px
      letter-spacing: 1px
      margin-left: 1.55%
      text-transform: uppercase
      transition: color .3s
      @media (max-width: 960px)
        margin-left: 28px
      @media (max-width: 400px)
        display: none
      &:hover
        color: $bronze-gold
    .mobile-menu
      display: flex
      align-items: center
      justify-content: flex-start
      flex: 1 1 auto
      @media (max-width: 960px)
        transform: translateX(100vw)
        transition: transform .3s
        position: absolute
        top: 100%
        right: 0
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        background-color: $dark-gray
        max-width: 320px
        width: 100%
        padding: 0 20px
        height: 100vh
        overflow-y: scroll
        @media (max-width: 414px)
          max-width: 100%
        .main-menu
          margin: 0
          width: 100%
          &__list
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
          &__item
            width: 100%
            flex: 0 0 100%
            max-width: 100%
            margin: 0
            border-bottom: 1px solid #595e61
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: block
            width: 100%
            font-size: 14px
            line-height: 20px
            font-weight: 400
            letter-spacing: 2px
            padding: 12px 0
            margin: 5px 0
        &.show
          transform: translateX(0)
    .main-menu
      margin-left: 2.75%
      &__list
        display: flex
        align-items: center
        justify-content: flex-start
      &__item
        flex: 0 0 auto
        width: auto
        margin-left: 30px
        &:nth-of-type(1)
          margin-left: 0
      &__link
        display: inline-block
        color: #fff
        font-weight: 600
        font-size: 12px
        line-height: 12px
        letter-spacing: 1px
        text-transform: uppercase
        transition: color .3s
        padding: 8px 0
        &:hover
          color: $bronze-gold
    .backcall-block
      display: flex
      align-items: center
      justify-content: flex-end
      margin-left: auto
      flex: 0 0 auto
      width: auto
      @media (max-width: 960px)
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        width: 100%
      .phone-link
        display: inline-block
        color: #fff
        font-size: 20px
        line-height: 20px
        letter-spacing: 1px
        transition: color .3s
        margin-right: 40px
        position: relative
        @media (max-width: 960px)
          display: inline-block
          margin: 20px 0 0
        &:hover
          color: $bronze-gold
        &:after
          content: ''
          display: block
          width: 1px
          height: 24px
          background-color: rgba(#fff, .2)
          position: absolute
          left: calc(100% + 20px)
          top: -2px
          @media (max-width: 960px)
            display: none
      .profile-link
        display: inline-block
        color: #fff
        transition: color .3s
        font-size: 18px
        line-height: 20px
        margin-right: 44px
        @media (max-width: 960px)
          order: -1
          display: flex
          align-items: center
          justify-content: space-between
          width: 100%
          padding: 5px 0
          margin: 5px 0
          border-bottom: 1px solid #595e61
          &:before
            content: attr(data-name)
            color: #fff
            font-size: 14px
            line-height: 40px
            display: inline-block
            text-transform: uppercase
            transition: color .3s
        &:hover
          color: $bronze-gold
          &:before
            color: $bronze-gold
      .test-link
        display: inline-flex
        align-items: center
        justify-content: center
        border: 1px solid #fff
        padding: 16px 28px
        text-transform: uppercase
        font-weight: 600
        font-size: 11px
        line-height: 18px
        letter-spacing: 2px
        color: #fff
        transition: background-color .3s, color .3s, border-color .3s
        @media (max-width: 960px)
          display: flex
          width: 100%
          justify-content: center
          margin-top: 30px
        &:after
          content: '\e800'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0 0 0 10px
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $bronze-gold
          font-size: 18px
          line-height: 20px
          transition: transform .3s, color .3s
        &:hover
          background-color: $bronze-gold
          color: #fff
          border-color: $bronze-gold
          &:after
            color: #fff
            transform: translateX(5px)
    @import "hamburger"
  &.scroll
    padding: 5px 0
