[class^="icon-"]:before,
[class*=" icon-"]:before
  font-family: Fontello
  font-style: normal
  font-weight: normal
  speak: never
  display: block
  margin: 0
  text-decoration: inherit
  text-align: center
  font-variant: normal
  text-transform: none

/* your fontello items here */

.icon-arrow-icon:before
  content: '\e800'
.icon-close-icon:before
  content: '\e801'
.icon-fb-icon:before
  content: '\e802'
.icon-inst-icon:before
  content: '\e803'
.icon-mail-icon:before
  content: '\e804'
.icon-marker-icon:before
  content: '\e805'
.icon-phone-icon:before
  content: '\e806'
.icon-play-icon:before
  content: '\e807'
.icon-time-icon:before
  content: '\e809'
.icon-user-icon:before
  content: '\e80a'
.icon-vk-icon:before
  content: '\e80b'
.icon-yt-icon:before
  content: '\e80c'
.icon-plus-icon:before
  content: '\e80d'