}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #30363A;
  background-color: #FFF;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #30363A;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #C78E66;
}
body::-webkit-scrollbar-track {
  background: #F2F2F2;
  border-radius: 0;
}
body {
  scrollbar-face-color: #30363A;
  scrollbar-track-color: #F2F2F2;
}
body.overflow {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1735px;
  padding: 0 20px;
}
@media (max-width: 360px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  background-color: #30363A;
  padding: 13px 0;
  z-index: 10;
  transition: padding 0.3s;
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 1840px;
  margin: 0 auto;
  /* settings */
  /* slide animate */
}
@media (max-width: 1840px) {
  .header__content {
    padding: 0 15px;
  }
}
.header__content .logo-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.header__content .logo-link:after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 26px;
  background-color: #C78E66;
  margin: 5px 0 5px 28px;
}
@media (max-width: 380px) {
  .header__content .logo-link:after {
    margin: 5px 0 5px 15px;
  }
}
.header__content .logo-link .logo-image {
  display: inline-block;
  width: auto;
  max-width: 100%;
}
@media (max-width: 360px) {
  .header__content .logo-link .logo-image {
    max-width: 100px;
  }
}
.header__content .add-link {
  display: inline-block;
  color: #fff;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 1px;
  margin-left: 1.55%;
  text-transform: uppercase;
  transition: color 0.3s;
}
@media (max-width: 960px) {
  .header__content .add-link {
    margin-left: 28px;
  }
}
@media (max-width: 400px) {
  .header__content .add-link {
    display: none;
  }
}
.header__content .add-link:hover {
  color: #C78E66;
}
.header__content .mobile-menu {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1 auto;
}
@media (max-width: 960px) {
  .header__content .mobile-menu {
    transform: translateX(100vw);
    transition: transform 0.3s;
    position: absolute;
    top: 100%;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background-color: #30363A;
    max-width: 320px;
    width: 100%;
    padding: 0 20px;
    height: 100vh;
    overflow-y: scroll;
  }
}
@media (max-width: 960px) and (max-width: 414px) {
  .header__content .mobile-menu {
    max-width: 100%;
  }
}
@media (max-width: 960px) {
  .header__content .mobile-menu .main-menu {
    margin: 0;
    width: 100%;
  }
  .header__content .mobile-menu .main-menu__list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .header__content .mobile-menu .main-menu__item {
    width: 100%;
    flex: 0 0 100%;
    max-width: 100%;
    margin: 0;
    border-bottom: 1px solid #595e61;
  }
  .header__content .mobile-menu .main-menu__item:nth-of-type(1) {
    margin-top: 0;
  }
  .header__content .mobile-menu .main-menu__link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    letter-spacing: 2px;
    padding: 12px 0;
    margin: 5px 0;
  }
}
@media (max-width: 960px) {
  .header__content .mobile-menu.show {
    transform: translateX(0);
  }
}
.header__content .main-menu {
  margin-left: 2.75%;
}
.header__content .main-menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.header__content .main-menu__item {
  flex: 0 0 auto;
  width: auto;
  margin-left: 30px;
}
.header__content .main-menu__item:nth-of-type(1) {
  margin-left: 0;
}
.header__content .main-menu__link {
  display: inline-block;
  color: #fff;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: color 0.3s;
  padding: 8px 0;
}
.header__content .main-menu__link:hover {
  color: #C78E66;
}
.header__content .backcall-block {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-left: auto;
  flex: 0 0 auto;
  width: auto;
}
@media (max-width: 960px) {
  .header__content .backcall-block {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
  }
}
.header__content .backcall-block .phone-link {
  display: inline-block;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 1px;
  transition: color 0.3s;
  margin-right: 40px;
  position: relative;
}
@media (max-width: 960px) {
  .header__content .backcall-block .phone-link {
    display: inline-block;
    margin: 20px 0 0;
  }
}
.header__content .backcall-block .phone-link:hover {
  color: #C78E66;
}
.header__content .backcall-block .phone-link:after {
  content: "";
  display: block;
  width: 1px;
  height: 24px;
  background-color: rgba(255, 255, 255, 0.2);
  position: absolute;
  left: calc(100% + 20px);
  top: -2px;
}
@media (max-width: 960px) {
  .header__content .backcall-block .phone-link:after {
    display: none;
  }
}
.header__content .backcall-block .profile-link {
  display: inline-block;
  color: #fff;
  transition: color 0.3s;
  font-size: 18px;
  line-height: 20px;
  margin-right: 44px;
}
@media (max-width: 960px) {
  .header__content .backcall-block .profile-link {
    order: -1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 5px 0;
    margin: 5px 0;
    border-bottom: 1px solid #595e61;
  }
  .header__content .backcall-block .profile-link:before {
    content: attr(data-name);
    color: #fff;
    font-size: 14px;
    line-height: 40px;
    display: inline-block;
    text-transform: uppercase;
    transition: color 0.3s;
  }
}
.header__content .backcall-block .profile-link:hover {
  color: #C78E66;
}
.header__content .backcall-block .profile-link:hover:before {
  color: #C78E66;
}
.header__content .backcall-block .test-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #fff;
  padding: 16px 28px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 11px;
  line-height: 18px;
  letter-spacing: 2px;
  color: #fff;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
@media (max-width: 960px) {
  .header__content .backcall-block .test-link {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 30px;
  }
}
.header__content .backcall-block .test-link:after {
  content: "\e800";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0 0 0 10px;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  color: #C78E66;
  font-size: 18px;
  line-height: 20px;
  transition: transform 0.3s, color 0.3s;
}
.header__content .backcall-block .test-link:hover {
  background-color: #C78E66;
  color: #fff;
  border-color: #C78E66;
}
.header__content .backcall-block .test-link:hover:after {
  color: #fff;
  transform: translateX(5px);
}
.header__content .hamburger {
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  z-index: 20;
  margin: 0 0 0 auto;
}
@media (max-width: 960px) {
  .header__content .hamburger {
    display: inline-flex !important;
  }
}
.header__content .hamburger:hover {
  opacity: 1;
}
.header__content .hamburger.is-active:hover {
  opacity: 1;
}
.header__content .hamburger.is-active .hamburger-inner {
  background-color: #fff;
}
.header__content .hamburger.is-active .hamburger-inner::before, .header__content .hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}
.header__content .hamburger-box {
  width: 32px;
  height: 45px;
  display: inline-block;
  position: relative;
}
.header__content .hamburger-inner {
  display: block;
  top: 50%;
  width: 32px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-timing-function: ease;
  transition: transform 0.15s;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  width: 32px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  content: "";
  display: block;
}
.header__content .hamburger-inner::before {
  top: -10px;
}
.header__content .hamburger--slider .hamburger-inner {
  top: 12px;
}
.header__content .hamburger--slider .hamburger-inner::before {
  top: 8px;
  right: 0;
  width: 32px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.header__content .hamburger--slider .hamburger-inner::after {
  top: 16px;
}
.header__content .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(4px, 8px, 0) rotate(45deg);
  height: 2px;
  width: 25px;
}
.header__content .hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.header__content .hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -16px, 0) rotate(-90deg);
  height: 2px;
  width: 25px;
}
.header.scroll {
  padding: 5px 0;
}

/* main page */
.main-banner {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: calc(100vh - 80px);
  margin-top: 80px;
  position: relative;
}
@media (max-width: 960px) {
  .main-banner {
    margin-top: 62px;
    height: calc(100vh - 62px);
    height: calc(var(--vh, 1vh) * 100 - 62px);
  }
}
.main-banner .container {
  padding-top: 5.65%;
  position: relative;
  z-index: 1;
}
.main-banner__title {
  text-align: center;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 400;
  color: #181B1D;
}
@media (max-width: 768px) {
  .main-banner__title {
    font-size: 24px;
    line-height: 25px;
  }
}
@media (max-width: 414px) {
  .main-banner__title {
    font-size: 22px;
    line-height: 20px;
  }
}
@media (max-width: 360px) {
  .main-banner__title {
    font-size: 18px;
    line-height: 20px;
  }
}
.main-banner__title span {
  display: block;
  font-weight: 300;
  font-size: 80px;
  line-height: 80px;
  margin-top: 10px;
  letter-spacing: 0;
}
@media (max-width: 1400px) {
  .main-banner__title span {
    font-size: 60px;
    line-height: 60px;
  }
}
@media (max-width: 768px) {
  .main-banner__title span {
    font-size: 50px;
    line-height: 50px;
  }
}
@media (max-width: 414px) {
  .main-banner__title span {
    font-size: 35px;
    line-height: 35px;
  }
}
@media (max-width: 360px) {
  .main-banner__title span {
    font-size: 30px;
    line-height: 30px;
  }
}
.main-banner .scroll-btn {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}